/* ==========================================================================
   Header
   ========================================================================== */
.header__container {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	background-color: rgba(255,255,255, 0.8);
	z-index: 9999;
	display: flex;
	justify-content: space-between;
	min-height: 75px;
}
.header__left {
	display: flex;
	align-items: center;
}
.header__logo {
	// display: inline-block;
	line-height: 0;
	margin: 0 15px;
}
.header__nav {
	li {
		display: inline-block;
		margin-right: 15px;
		&:last-child {
			margin-right: 0;
		}
	}
}
.header__search {
	height: 100%;
	margin-right: 15px;
	form {
		height: 100%;
		display: flex;
		align-items: center;
	}
	input[type="submit"] {
		height: 36px;
		top: -6px;
		position: relative;
		margin-left: 5px;
	}
}
